.background {
  background-color: aquamarine;
  width: 100%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.background button {
  border: none;
  font-size: 70px;
  border-radius: 50px;
  width: 300px;
  height: 200px;
  transition: 1s;
}
.background button:hover {
  transition: 1s;
  cursor: pointer;
  color: aquamarine;
  background-color: darkslateblue;
}

